  {\n                                    title: \"Error\",\n                                    message: \"タスクキューの削除に失敗しました\"\n                                }\n                            );\n                        }\n                    });\n            },\n            getListData: function () {\n                axios.get(\"/api/twilio/taskqueues\")\n                    .then(response => {\n                        this.task_queue_datas = response.data;\n                    });\n            }\n        }\n    }\n</script>\n\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\n<style scoped>\n    h1,\n    h2 {\n        font-weight: normal;\n    }\n\n    a {\n        color: #42b983;\n    }\n\n    el-table-column:first-child {\n        text-align: center;\n    }\n\n</style>"],"sourceRoot":""}]);

