/show/\" + this.$route.params.sid)\n                .then(response => {\n                    console.log(response.data.status);\n                    if (response.data.status === 'OK') {\n                        this.form.name = response.data.friendlyName;\n                        this.form.available = response.data.available;\n                    } else {\n                        console.log('アクティビティ情報の取得に失敗しました');\n                        Notification.error(\n                            {\n                                title: \"Error\",\n                                message: \"アクティビティ情報の取得に失敗しました\"\n                            }\n                        );\n                    }\n                });\n        },\n        methods: {\n            onSubmit() {\n                axios.put(\"/api/twilio/activities/update\", JSON.stringify({\n                        sid: this.$route.params.sid,\n                        name: this.form.name,\n                        available: this.form.available,\n                    }))\n                    .then(response => {\n                        console.log(response.data.status);\n                        if (response.data.status === 'OK') {\n                            console.log('アクティビティの更新に成功しました');\n                            location.href = '/#/activities';\n                            Notification.success(\n                                {\n                                    title: \"Success\",\n                                    message: \"アクティビティを更新しました。\"\n                                }\n                            );\n                        } else {\n                            console.log('アクティビティの更新に失敗しました');\n                            Notification.error(\n                                {\n                                    title: \"Error\",\n                                    message: \"アクティビティの登録に失敗しました\"\n                                }\n                            );\n                        }\n                    });\n            },\n            onCancel() {\n                location.href = '/#/activities';\n            }\n        }\n    }\n</script>\n\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\n<style scoped>\n    h1,\n    h2 {\n        font-weight: normal;\n    }\n\n    a {\n        color: #42b983;\n    }\n\n    el-table-column:first-child {\n        text-align: center;\n    }\n    .el-form-item__content {\n        text-align: left;\n    }\n</style>"],"sourceRoot":""}]);

